export default {
	props: {
		startTrainName: {
			type: "",
			default: ""
		},
		endTrainName: {
			type: "",
			default: ""
		},
		dateweek: {
			type: String,
			default: 'hello'
		},
		godate: {
			type: String,
			default: ''
		},
		endorse: {
			type: Boolean,
			default: false
		} //endorse是区分改签和查询页面的
	},
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500
		}
	},
	methods: {
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		startTrain(e) {
			uni.navigateTo({
				url: '../chooseCity/chooseCity?startTrainName=' + this.startTrainName + "&trainState=0"
			})
		},
		endTrain(e) {
			uni.navigateTo({
				url: '../chooseCity/chooseCity?endTrainName=' + this.endTrainName + "&trainState=1"
			})
		},
		chooseDate(e) {
			uni.navigateTo({
				url: '../chooseDate/chooseDate?defaultDate=' + this.godate
			})
		},
		chooseDateEndorse(e) {
			uni.navigateTo({
				url: '../chooseDate/chooseDate?defaultDate=' + this.godate
			})
		},
		
		endorseNavigator(){
			uni.redirectTo({
				url:'orderQueryTicket?date='+this.godate+'&week='+this.dateweek+'&startTrain='+this.startTrainName+'&endTrain='+this.endTrainName
			})
		}
		
	}
}
